import { HttpClient } from '@angular/common/http';
import { Component,Input, OnChanges, SimpleChanges } from '@angular/core';


@Component({
  selector: 'com-img',
  templateUrl: './imgLoader.component.html',
})
export class ImgLoaderComponent implements OnChanges {
  @Input() imgSrc="";
  @Input() title = "";
  public failLoader:boolean = true;
  public isLoading: boolean = true;
  constructor(
    private http: HttpClient
  ) {}

  ngOnChanges(changes: SimpleChanges): void {
    if(this.imgSrc){
      this.getImg()
    }
  }


  private getImg():void{
    const image = new Image();
    image.onload =  () => {
      this.failLoader = false;
      this.isLoading = false;
      console.log('load',this.imgSrc,image.complete)
      
    }
    image.onerror = ()=>{
      this.failLoader = true;
      this.isLoading = false;
      console.log('error',this.imgSrc,image.complete)
    }
    image.src = this.imgSrc;
    if(image.complete){
      // this.failLoader = false;
      // this.isLoading = false;
      console.log('complete',this.imgSrc)
    }
  }
 
}
