import { Component, EventEmitter, Input, Output } from '@angular/core';
import { SpotSimple } from '../class/SpotSimple';
import {SpotGroupType} from '../class/SpotGroupType'

@Component({
  selector: 'com-spot-card',
  templateUrl: './spotCard.component.html',
})
export class SpotCardComponent {
    @Input() spot:SpotSimple = {
      id:'',
        name: '',
        img:{
          url:'',
          title:''
        },
        openTime:'',
        typeList:[]
    }
    @Output() onClick = new EventEmitter();
    public outPutClick():void{
      this.onClick.emit()
    }

    public getTagClass(tag:SpotGroupType):string{
      const tagMap = {
        [SpotGroupType.NATURE]:'cus-tag--nature',
        [SpotGroupType.FARM]:'cus-tag--farm',
        [SpotGroupType.CULTURE]:'cus-tag--culture',
        [SpotGroupType.OTHER]:'cus-tag--other'
      }
      return tagMap[tag]
    }
}
