import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { Observable, of } from 'rxjs'
import { map } from 'rxjs/operators'
import * as hmacsha1 from 'hmacsha1'
import { SpotDetail } from './class/SpotDetail'
import { SpotSimple } from './class/SpotSimple'
import { SpotGroupType } from './class/SpotGroupType'
import { SpotClass } from './class/SpotClass'
import { RecommendFilterDto } from './class/RecommendFilterDto'
import { RestaurantDto } from './class/RestaurantDto'
import { FilterDto } from './component/filter.component'
import { groupList } from './component/groupSelect.component'

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  constructor(private http: HttpClient) {}

  public getSpotlight(): Observable<SpotSimple[]> {
    const filterName = [
      'C1_379000000A_000217',
      'C1_315081300H_000114',
      'C1_315081300H_000088',
      'C1_379000000A_000019',
      'C1_376480000A_000304',
      'C1_379000000A_000023',
      'C1_397000000A_000637',
      'C1_315081600H_000309',
    ]
    const filterParam = filterName.map((d) => `ID eq '${d}'`).join('or ')
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$filter=${filterParam}&$format=JSON`,
    ).pipe(map((res) => this.apiDtoToSpotSimple(res)))
  }

  public getRecommendFilterDto(): Observable<RecommendFilterDto[]> {
    const selectParam = `$select=Class1,Picture,Name`
    const randomStart = Math.round(Math.random() * 1000)
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?${selectParam}&$filter=Picture/PictureUrl1 ne null and Class1 ne null&$format=JSON&$top=6&$skip=${randomStart}`,
    ).pipe(
      map((res) =>
        res.map((d) => {
          return {
            imgUrl: d.Picture ? d.Picture.PictureUrl1 : '',
            filterParam: this.apiClassToGroupType([d.Class1])[0],
            title: d.Name
          }
        }),
      ),
    )
  }

  public getAllDataByFilter(
    filterDto: FilterDto,
    page: number,
  ):Observable<SpotSimple[]> {
    if(this.filterDtoIsComplex(filterDto)){
      return this.getFilterDataByClass(filterDto,page)
    }else{
      return this.getRealApiByFilter(filterDto,page)
    }
  }

  private filterDtoIsComplex( filterDto: FilterDto,):boolean{
    return Boolean(filterDto.group ? filterDto.openTime||filterDto.ticket:false);
  }
  public getRealApiByFilter(
    filterDto: FilterDto,
    page: number,
  ): Observable<SpotSimple[]> {
    const cityParam = filterDto.city ? `/${filterDto.city}` : ''
    const filterParam = this.getParamUrlByFilter(filterDto)
    const filter = filterParam ? `&$filter=${filterParam}` : ''
    const skip = (page - 1) * 12
    const baseUrl = `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot${cityParam}?${filter}&$format=JSON`
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot${cityParam}?${filter}&$format=JSON&$top=12&$skip=${skip}`,
    ).pipe(map((res) => this.apiDtoToSpotSimple(res)))
  }

  
  public getFilterDataNumber( filterDto: FilterDto):Observable<number>{
    if(this.filterDtoIsComplex(filterDto)){
      return this.getFilterDataNumByClass(filterDto)
    }else{
      return this.getRealApiDataNumber(filterDto)
    }
  }
  public getRealApiDataNumber( filterDto: FilterDto):Observable<number>{
    const selectParam = `$select=ID`
    const cityParam = filterDto.city ? `/${filterDto.city}` : ''
    const filterParam = this.getParamUrlByFilter(filterDto)
    const filter = filterParam ? `&$filter=${filterParam}` : ''
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot${cityParam}?${selectParam}${filter}&$format=JSON`,
    ).pipe(map((res) => res.length))
  }
  
  public getFilterDataByClass( filterDto: FilterDto,page:number  ):Observable<SpotSimple[]>{
   const group = filterDto.group as SpotGroupType
   return this.getAllAndNoClass(filterDto,group).pipe(map(res=>{
     return res.filter((d,i)=>{
       const pageStart = (page-1)*12;
       const pageEnd = (page)*12;
       return i>= pageStart && i<pageEnd
     })
   }))
  }
  public getFilterDataNumByClass( filterDto: FilterDto ):Observable<number>{
    const group = filterDto.group as SpotGroupType
    return this.getAllAndNoClass(filterDto,group).pipe(map(res=>{
      return res.length
    }))
   }

  private getAllAndNoClass(filterDto: FilterDto,group:SpotGroupType):Observable<SpotSimple[]>{
    const cityParam = filterDto.city ? `/${filterDto.city}` : ''
    const newFilterDto = {
      ...filterDto,
      group: ''
    }
    const filterParam = this.getParamUrlByFilter(newFilterDto)
    const filter = filterParam ? `&$filter=${filterParam}` : ''
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot${cityParam}?${filter}&$format=JSON`,
    ).pipe(map((res) => this.apiDtoToSpotSimple(res).filter(d=>d.typeList.includes(group))))
  }



  private getParamUrlByFilter(filterDto: FilterDto): string {
    const filterText = filterDto.searchText
      ? `contains(Name,'${filterDto.searchText}')`
      : ''
    const group = this.getGroupList(filterDto.group)
    const ticketInfo = this.getTicketInfo(filterDto.ticket);
    const openInfo = this.getOpenInfo(filterDto.openTime);
    const allCondition = [filterText, group, ticketInfo, openInfo]
      .filter((d) => d)
      .join(' and ')
    console.log(allCondition)
    return `${allCondition}`
  }

  private getTicketInfo(ticket: string): string {
    let keyWordList = []
    if (ticket === '免費') {
      keyWordList = ['免費']
    }
    if (ticket === '收費') {
      keyWordList = ['元']
    }
    return keyWordList.length
      ? keyWordList.map((d) => `contains(TicketInfo,'${d}')`).join(' or ')
      : ''
  }
  private getOpenInfo(ticket: string): string {
    let keyWordList = []
    if (ticket === '全年') {
      keyWordList = ['全年']
    }
    if(ticket === '預約'){
      keyWordList = ['預約']
    }
    return keyWordList.length
      ? keyWordList.map((d) => `contains(OpenTime,'${d}')`).join(' or ')
      : ''
  }
  private getGroupList(group: string): string {
    const groupType = typeList.find((d) => d.groupType === group)
    if (groupType) {
      return groupType.list
        .map(
          (d) =>
            `contains(Class1,'${d}') or contains(Class2,'${d}') or contains(Class3,'${d}')`,
        )
        .join('or ')
    } else {
      return ''
    }
  }
  public getNearbyRestaurant(
    lat: number,
    lon: number,
  ): Observable<RestaurantDto[]> {
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/Restaurant?$spatialFilter=nearby(${lat},${lon},1000)&$format=JSON`,
    ).pipe(
      map((res) =>
        res.map((d) => {
          const restLon = d.Position.PositionLon
          const resLat = d.Position.PositionLat
          return {
            id: d.ID,
            name: d.Name,
            position: Math.round(this.getDistance(lat, lon, resLat, restLon)),
            imgUrl: d.Picture ? d.Picture.PictureUrl1 : '',
            address: d.Address,
            openTime: d.OpenTime,
            description: d.Description,
          }
        }),
      ),
    )
  }

  public getSpotDetailById(id: string): Observable<SpotDetail> {
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$filter=ID eq '${id}'&$format=JSON`,
    ).pipe(map((res) => this.apiDtoToSpotDetail(res[0])))
  }
  private getApiByUrl(url: string): Observable<any> {
    return this.http.get(url, { headers: this.getHeaders() })
  }

  private apiDtoToSpotSimple(resData: Array<any>): SpotSimple[] {
    return resData.map((d) => {
      return {
        id: d.ID,
        name: d.Name,
        img: {
          url: d.Picture ? d.Picture.PictureUrl1 : '',
          title: d.Picture ? d.Picture.PictureDescription1 : '',
        },
        openTime: d.OpenTime,
        typeList: this.apiClassToGroupType([
          d.Class1,
          d.Class2,
          d.Class3,
        ] as Array<SpotClass | undefined>),
      }
    })
  }

  private apiDtoToSpotDetail(resData: any): SpotDetail {
    const imgUrl = [
      {
        url: resData.Picture ? resData.Picture.PictureUrl1 : '',
        title: resData.Picture ? resData.Picture.PictureDescription1 : '',
      },
      {
        url: resData.Picture ? resData.Picture.PictureUrl2 : '',
        title: resData.Picture ? resData.Picture.PictureDescription2 : '',
      },
      {
        url: resData.Picture ? resData.Picture.PictureUrl3 : '',
        title: resData.Picture ? resData.Picture.PictureDescription3 : '',
      },
    ]
    return {
      id: resData.ID,
      name: resData.Name,
      imgUrlList: imgUrl,
      city: resData.City,
      address: resData.Address,
      zipCode: resData.ZipCode,
      websiteUrl: resData.WebsiteUrl,
      descriptionDetail: resData.DescriptionDetail,
      phone: resData.Phone,
      updateTime: resData.UpdateTime,
      ticketInfo: resData.TicketInfo,
      position: {
        lon: resData.Position.PositionLon,
        lat: resData.Position.PositionLat,
      },
      parkingInfo: resData.ParkingInfo,
      openTime: resData.OpenTime,
      typeList: this.apiClassToGroupType([
        resData.Class1,
        resData.Class2,
        resData.Class3,
      ] as Array<SpotClass | undefined>),
    }
  }
  private getHeaders(): HttpHeaders {
    const gmtTime = new Date().toUTCString()
    const hash = hmacsha1('XnkO4Q6oiRxv8Q7LfXsO-E_ECug', `x-date: ${gmtTime}`)
    const headers: HttpHeaders = new HttpHeaders({
      'Content-Type': 'application/json',
      Authorization: `hmac username="924170cd868348de90cf7b30f8cda570", algorithm="hmac-sha1", headers="x-date", signature="${hash}"`,
      'x-date': gmtTime,
    })
    return headers
  }

  private apiClassToGroupType(
    classList: Array<SpotClass | undefined>,
  ): SpotGroupType[] {
    const newObject = {}
    classList.forEach((d) => {
      if (d) {
        const inGroup = typeList.find((typeDto) => typeDto.list.includes(d))
        if (inGroup) {
          newObject[inGroup.groupType] = 1
        } else {
          newObject[SpotGroupType.OTHER] = 1
        }
      }
    })
    return Object.keys(newObject) as SpotGroupType[]
  }

  private getDistance(lat1, lon1, lat2, lon2): number {
    if (lat1 == lat2 && lon1 == lon2) {
      return 0
    } else {
      var radlat1 = (Math.PI * lat1) / 180
      var radlat2 = (Math.PI * lat2) / 180
      var theta = lon1 - lon2
      var radtheta = (Math.PI * theta) / 180
      var dist =
        Math.sin(radlat1) * Math.sin(radlat2) +
        Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta)
      if (dist > 1) {
        dist = 1
      }
      dist = Math.acos(dist)
      dist = (dist * 180) / Math.PI
      dist = dist * 60 * 1.1515
      dist = dist * 1.609344 * 1000
      return dist
    }
  }
}

const typeList = [
  {
    groupType: SpotGroupType.NATURE,
    list: [
      SpotClass.NATURAL,
      SpotClass.METROPOLITAN_PARK,
      SpotClass.NATION_SCENE,
      SpotClass.FOREST,
      SpotClass.NATION_PARK,
    ],
  },
  {
    groupType: SpotGroupType.FARM,
    list: [
      SpotClass.ECOLOGICAL,
      SpotClass.RECREATION,
      SpotClass.TOURIST_FACTORY,
      SpotClass.FOREST_FARM,
      SpotClass.AGRICULTURAL_RECREATION,
    ],
  },
  {
    groupType: SpotGroupType.CULTURE,
    list: [
      SpotClass.CULTURE,
      SpotClass.TEMPLE,
      SpotClass.SPA,
      SpotClass.EAT,
      SpotClass.ART,
    ],
  },
  {
    groupType: SpotGroupType.OTHER,
    list: [SpotClass.SPORT, SpotClass.OTHER],
  },
]
