import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../apiService.service';
import { RecommendFilterDto } from '../class/RecommendFilterDto';
import { SpotGroupType } from '../class/SpotGroupType';
import { SpotSimple} from '../class/SpotSimple'
import { FilterDto } from '../component/filter.component';
@Component({
  selector: 'com-home',
  templateUrl: './home.component.html',
})
export class HomeComponent implements OnInit{
 public spotlightList:SpotSimple[] = [];
 public filterList: RecommendFilterDto[]=[];
constructor(
  private apiService:ApiService,
  private router: Router
){

}
  ngOnInit(): void {
    this.apiService.getSpotlight().subscribe(d=>{
      this.spotlightList = d;
    })

    this.apiService.getRecommendFilterDto().subscribe(d=>{
      console.log(d)
      this.filterList = d;
    })
  }

  public viewDetail(spotId:string):void{
    window.open(`detail/${spotId}`, "_blank");
  }

  public goFilter(filterData:FilterDto):void{
    this.router.navigate(['search',{
      ...filterData,
      page:1
    }])
  }

  public goFilterByTag(group:SpotGroupType):void{
    const filter: FilterDto = {
      city: '',
      group,
      searchText: '',
      openTime: '',
      ticket: ''
    }
    this.router.navigate(['search',{
      ...filter,
      page:1
    }])
  }
}



