import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core'
import { SpotGroupType } from '../class/SpotGroupType'
import { FilterDto } from './filter.component'
import { ValueItem } from './select.component'

@Component({
  selector: 'com-group-select',
  templateUrl: './groupSelect.component.html',
})
export class GroupSelectComponent implements OnChanges  {
 
  @Input() filterObj: FilterDto = {
    city: '',
    group: '',
    searchText: '',
    openTime: '',
    ticket: '',
  }
  public selfFilterDTo: FilterDto = {
    city: '',
    group: '',
    searchText: '',
    openTime: '',
    ticket: '',
  }

  public ticketList = ticketList;
  public openList = openList;

  @Output() doFilter = new EventEmitter()
  
  ngOnChanges(changes: SimpleChanges): void {
    this.selfFilterDTo = JSON.parse(JSON.stringify(this.filterObj))
  }
  

  public cityList = cityList
  public groupList = groupList

  public search(): void {
    this.doFilter.emit(this.selfFilterDTo)
  }

  public clearOtherFilter():void{
    this.selfFilterDTo = {
      ...this.filterObj,
      city:'',
      group: '',
      openTime: '',
      ticket:''
    }
  }
}

