import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
  ViewChild,
} from '@angular/core'
import { cityList, groupList } from './groupSelect.component'
import { ValueItem } from './select.component'
import * as $ from 'jquery'

@Component({
  selector: 'com-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.scss'],
})
export class FilterComponent implements AfterViewInit, OnDestroy {
  @Output() doFilter = new EventEmitter()
  @ViewChild('filterDropDown') filterDropDown!: ElementRef
  public showList = false
  @Input() filterObj: FilterDto = {
    city: '',
    group: '',
    searchText: '',
    openTime: '',
    ticket: ''
  }
  public cityList = cityList
  public groupList = groupList
  constructor(private elementRef: ElementRef) {}

  ngAfterViewInit(): void {
    $(document).on('click', 'body', (event) => {
      if (
        this.filterDropDown &&
        !$.contains(this.filterDropDown.nativeElement, event.target) &&
        this.filterDropDown.nativeElement !== event.target
      ) {
        this.showList = false
      }
    })
  }

  ngOnDestroy(): void {}
  public getFilterTag(): ValueItem[] {
    return Object.keys(this.filterObj)
      .map((key) => {
        let name = this.filterObj[key]
        if (key === 'city') {
          const city = cityList.find((d) => d.key === name)
          name = city ? city.name : name
        }
        return {
          key,
          name,
        }
      })
      .filter((d) => d.name)
  }
  public search(): void {
    this.doFilter.emit(this.filterObj)
  }

  public deleteTag(tag: ValueItem) {
    this.filterObj[tag.key] = ''
  }
}

export interface FilterDto {
  city: string;
  group: string;
  searchText: string;
  openTime: string;
  ticket: string;
}
