import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output } from "@angular/core";
import * as $ from 'jquery';
@Component({
  selector: "com-select",
  templateUrl: "./select.component.html",
  styleUrls: ["./select.scss"],
})
export class SelectComponent implements AfterViewInit, OnDestroy{
  @Input() placeholder = '請輸入';
    @Input() hasFilter= false;
  @Output() onSelect = new EventEmitter();
  @Input() value= '';
  @Input() valueList: ValueItem[] = [];
  public showList = false;
  public filterText = '';
  constructor(private elementRef:ElementRef){   
}

  ngAfterViewInit(): void {
    $(document).on('click','body',(event)=>{
     if(this.elementRef&&!$.contains(this.elementRef.nativeElement,event.target)&&this.elementRef.nativeElement!==event.target){
       this.showList = false
     }
    });
  }
  ngOnDestroy(): void {
  }



  public selectItem(item: ValueItem): void {
    this.value = item.key;
    this.onSelect.emit(item.key);
    this.filterText = '';
    this.showList = false;
  }

  public getCurrentName():string{
    const current = this.valueList.find(d=>d.key===this.value);
    return current ? current.name:''
  }

}

export interface ValueItem {
  icon?: string;
  name: string;
  key: string;
}
