import { Component, OnInit } from '@angular/core'
import {MatDialog} from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';

import { ApiService } from '../apiService.service'
import { RestaurantDto } from '../class/RestaurantDto'
import { SpotDetail } from '../class/SpotDetail'
import { TitleDto } from '../class/TitleDto'
import { RestaurantComponent } from '../component/restaurant.component'

@Component({
  selector: 'com-detail',
  templateUrl: './detail.component.html',
})
export class DetailComponent implements OnInit {
  restaurantList: RestaurantDto[] =[]
  spot: SpotDetail = {
    id: '',
    name: '',
    imgUrlList: [],
    city: '',
    address: '',
    zipCode: '',
    websiteUrl: '',
    descriptionDetail: '',
    openTime: '',
    phone: '',
    typeList: [],
    updateTime: '',
    ticketInfo: '',
    position: {
      lat: 0,
      lon: 0,
    },
    parkingInfo: '',
  }
  infoList: TitleDto[] = [
    {
      type: 'text',
      title: '地址',
      iconClass: 'icon-address',
      key:'address'
    },
    {
      type: 'text',
      title: '停車資訊',
      iconClass: 'icon-car',
      key:'parkingInfo'
    },
    {
      type: 'text',
      title: '營業時間',
      iconClass: 'icon-date',
      key:'openTime'
    },
    {
      type: 'text',
      title: '服務電話',
      iconClass: 'icon-phone',
      key:'phone'
    },
    {
      type: 'text',
      title: '費用',
      iconClass: 'icon-dollar',
      key:'ticketInfo'
    },
    {
      type: 'link',
      title: '官網',
      iconClass: 'icon-website',
      key:'websiteUrl'
    },
  ]

  constructor(
    private apiService: ApiService,
    public dialog: MatDialog,
    private activeRoute: ActivatedRoute,
    ) {}
  ngOnInit(): void {
    this.activeRoute.params.subscribe(d => {
      const id = d.id;
      if (id) {
        this.apiService.getSpotDetailById(id).subscribe((d) => {
          console.log(id)
          this.spot = d
          this.apiService.getNearbyRestaurant(d.position.lat,d.position.lon).subscribe(restList=>{
            this.restaurantList = restList.sort((a, b) => a.position - b.position)
          })
        })
      }
    });
    
  }

  public goToMap():void{
    window.open(`https://www.google.com/maps/search/?api=1&query=${this.spot.position.lat},${this.spot.position.lon}`, "_blank");
    
  }
  public openFoodDialog(food:RestaurantDto):void{
    const dialogRef = this.dialog.open(RestaurantComponent, {
      width: '880px',
      data:food,
      panelClass:['cus-model']
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }
}


