import { Component, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { ApiService } from '../apiService.service'
import { SpotSimple } from '../class/SpotSimple'
import { FilterDto } from '../component/filter.component'

@Component({
  selector: 'com-search',
  templateUrl: './search.component.html',
})
export class SearchComponent implements OnInit {
  public filterObj: FilterDto = {
    city: '',
    group: '',
    searchText: '',
    openTime: '',
    ticket: '',
  }
  public resultList: SpotSimple[] = []
  public pageNumList: number[] = [1, 2, 3, 4, 5]
  public currentPage: number = 1
  public resultNum: number = 1;
  constructor(
    private apiService: ApiService,
    private router: Router,
    private activeRoute: ActivatedRoute,
  ) {}

  ngOnInit(): void {
    this.activeRoute.paramMap.subscribe((v) => {
      this.filterObj = this.getObjFormParam()
      this.getData();
      this.setCurrentPage();
    })
  }

  public viewDetail(spotId: string): void {
    window.open(`detail/${spotId}`, '_blank')
  }

  private setCurrentPage(): void {
    this.apiService.getFilterDataNumber(this.getObjFormParam()).subscribe(num=>{
      this.resultNum = num;
      const page = Math.ceil(num/12);
      this.currentPage = Number(this.activeRoute.snapshot.paramMap.get('page'));
      this.pageNumList = [this.currentPage-2,this.currentPage-1,this.currentPage,this.currentPage+1,this.currentPage+2].filter(d=>d>0&&d<=page);
    })
  
  }
  private getData(): void {
    const page = Number(this.activeRoute.snapshot.paramMap.get('page'))
    this.apiService
      .getAllDataByFilter(this.getObjFormParam(), page)
      .subscribe((d) => {
        this.resultList = d
      })
  }

  private getObjFormParam(): FilterDto {
    return {
      city: this.activeRoute.snapshot.paramMap.get('city'),
      group: this.activeRoute.snapshot.paramMap.get('group'),
      searchText: this.activeRoute.snapshot.paramMap.get('searchText'),
      openTime: this.activeRoute.snapshot.paramMap.get('openTime'),
      ticket: this.activeRoute.snapshot.paramMap.get('ticket'),
    }
  }

  public goPage(pageNum: number): void {
    if (pageNum !== this.currentPage) {
      this.router.navigate([
        'search',
        {
          ...this.getObjFormParam(),
          page: pageNum,
        },
      ])
    }
  }

  public goFilter(filterData: FilterDto): void {
    this.router.navigate([
      'search',
      {
        ...filterData,
        page: 1,
      },
    ])
  }
}
